import VueRouter from "vue-router";
import HomePage from "@/pages/fayaHome/HomePage"
import PhotoPage from "@/pages/fayaPhoto/PhotoPage";
import VideoPage from "@/pages/fayaVideo/VideoPage";
import AboutPage from "@/pages/fayaAbout/AboutPage";
import ServicesPage from "@/pages/fayaServices/ServicesPage";
import ContactPage from "@/pages/fayaContact/ContactPage";
import NotFoundPage from "@/pages/fayaNotFound/NotFoundPage";
import LegalNotice from "@/pages/fayaTerms/LegalNotice";
import TermsConditions from "@/pages/fayaTerms/TermsConditions";
import PrivacyProtection from "@/pages/fayaTerms/PrivacyProtection";
import LandingPage1 from "@/pages/fayaLandingPages/LandingPage1";
import GalleryBlog1 from "@/pages/fayaBlogs/GalleryBlog1";
import Credits from "@/pages/fayaTerms/CreditsPage";

const router = new VueRouter({
    scrollBehavior() { return { x: 0, y: 0 }; },
    mode: "history",
    routes: [
        {
            path: "/",
            component: HomePage
        },
        {
            path: "/hochzeitsfotografie",
            component: PhotoPage
        },
        {
            path: "/hochzeitsvideografie",
            component: VideoPage
        },
        {
            path: "/leistungen",
            component: ServicesPage,
        },
        {
            path: "/about",
            component: AboutPage
        },
        {
            path: "/kontakt",
            component: ContactPage
        },
        {
            path: "/agb",
            component: TermsConditions
        },
        {
            path: "/datenschutz",
            component: PrivacyProtection
        },
        {
            path: "/impressum",
            component: LegalNotice
        },
        {
            path: "/credits",
            component: Credits
        },
        {
            path: "/blog",
            component: GalleryBlog1
        },
        {
            path: "/landingpage",
            component: LandingPage1
        },
        {
            path: "*",
            component: NotFoundPage
        }
    ]
})

export default router