<template>

<div class="contactBG">
  <div class="contactWrapper">
    <!-- HEAD -->
    <div class="cTitle">{{ heading1 }}</div>
    <div class="cSubtitle">{{ heading2 }}</div>
    <form>
      <!-- REASON - SELECT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="reason">Anlass <span>*</span></label>
        <select id="reason" name="reason" v-model="form.reason">
          <option disabled hidden value="">Whle aus</option>
          <template v-for="(reason, index) in selectReasons">
            <option :value="reason" v-bind:key="index">{{ reason }}</option>
          </template>
        </select>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- NAME - TEXT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="name">Name <span>*</span></label>
        <input id="name" name="name" v-model="form.name" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- EMAIL - TEXT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="email">E-Mail <span>*</span></label>
        <input id="email" name="email" v-model="form.email" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- PHONE - TEXT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="phone">Telefon <span>*</span></label>
        <input id="phone" name="phone" v-model="form.phone" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- DATE - DATE -->
      <div class="cRow">
        <label class="cLabelGeneral" for="date">Wunschdatum <span>*</span></label>
        <input id="date" name="date" v-model="form.date" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- LOCATION - TEXT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="location">Location(s) <span>*</span></label>
        <input id="location" name="location" v-model="form.location" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- SERVICE - SELECT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="service">Wir interessieren uns fr... <span>*</span></label>
        <select id="service" name="service" v-model="form.service">
          <option disabled hidden value="">Whle aus</option>
          <template v-for="(service, index) in selectServices">
            <option :value="service" v-bind:key="index">{{ service }}</option>
          </template>
        </select>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- STYLE - CHECKBOX RADIO -->
      <div class="cRow">
        <label class="cLabelGeneral">Stil der Hochzeit</label>
        <div class="cCheckboxes">
          <template v-for="(style, index) in checkboxesStyle">
            <div class="cCheckbox" v-bind:key="index">
              <input type="radio" :id="style.id" :value="style.style" v-model="form.style" />
              <label :for="style.id">{{ style.style }}</label>
            </div>
          </template>
        </div>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- MESSAGE - TEXTAREA -->
      <div class="cRow">
        <label class="cLabelGeneral" for="message">Nachricht <span>*</span></label>
        <textarea id="message" name="message" v-model="form.message" type="text"></textarea>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- NOTICE - SELECT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="notice">Wie habt ihr uns gefunden? <span>*</span></label>
        <select id="notice" name="notice" v-model="form.notice">
          <option disabled hidden value="">Whle aus</option>
          <template v-for="(notice, index) in selectNotices">
            <option :value="notice" v-bind:key="index">{{ notice }}</option>
          </template>
        </select>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- PRIVACY - CHECKBOX -->
      <div class="cRow">
        <label class="cLabelGeneral">Datenschutz <span>*</span></label>
        <div class="cTerms">
          <input type="checkbox" id="terms" v-model="form.terms" />
          <label for="terms">{{ privacyMessage }}</label>
        </div>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- BUTTON -->
      <button id="cButton" type="button" v-on:click="sendMail">{{ buttonMessage }}</button>
      <!-- LOADER -->
      <div id="cLoader">
        <img :src="loaderGIF" alt="Loader GIF">
        <div>{{ loaderMessage }}</div>
      </div>
      <!-- SUCCESS -->
      <div id="cSuccess" class="sentBox">
        {{ successMessage }} <router-link to="/">{{ sentMessage }}</router-link>
      </div>
      <!-- ERROR -->
      <div id="cError" class="sentBox">
        {{ errorMessage }} <router-link to="/">{{ sentMessage }}</router-link>
      </div>
    </form>
  </div>
</div>

</template>

<script>

import axios from "axios";

export default {
  name: "ContactForm1",
  props: [ "heading1" ],
  data() {
    return {
      heading2: "Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut" +
          " labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et" +
          " ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum" +
          " dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore " +
          "magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet " +
          "clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.",
      privacyMessage: "Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt" +
          " ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et" +
          " ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.",
      selectReasons: [ "Hochzeitsreportage", "Standesamtliche Trauung", "Sonstiger Anlass" ],
      selectServices: [ "Foto", "Video", "Foto & Video" ],
      selectNotices: [ "Empfehlung", "Instagram", "Facebook", "Google", "Sonstiges" ],
      checkboxesStyle: [
        { id: "klassisch", style: "Klassisch" },
        { id: "vintage", style: "Vintage" },
        { id: "boho", style: "Boho" },
        { id: "glam", style: "Glam" }
      ],
      errors: 0,
      loaderGIF: "/pub/img/deco/loader.gif",
      loaderMessage: "Nachricht wird gesendet. Bitte warten...",
      errorMessage: "Deine Nachricht konnte leider nicht versendet werden. Bitte versuche es spter erneut. ",
      successMessage: "Vielen Dank fr Deine Nachricht! Wir werden uns in Krze bei Dir melden. ",
      sentMessage: "Zur Startseite",
      buttonMessage: "Nachricht senden",
      requiredMessage: "Dieses Feld ist erforderlich!",
      axiosPostUrl: "http://127.0.0.1:5000/send",
      form: {
        reason: "",
        name: "",
        email: "",
        phone: "",
        date: "",
        location: "",
        service: "",
        style: "",
        message: "",
        notice: "",
        terms: ""
      },
    }
  },
  methods: {
    checkForm: function() {
      let requiredMessages = document.querySelectorAll(".cRequiredMessages");
      let counter = 0;
      this.errors = 0;

      requiredMessages.forEach(reqField => {
        reqField.style.display = "none";
      });

      // IMPORTANT: MUST BE IN THE ORDER OF THE LABELS IN HTML
      if (this.form.reason === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.name === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.email === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.phone === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.date === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.location === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.service === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      //if (this.form.style === "") {
      //  this.errors += 1;
      //  requiredMessages[counter].style.display = "flex";
      //}
      counter++;
      if (this.form.message === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.notice === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.terms !== true) {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
    },
    sendMail: function() {
      this.checkForm()
      if (this.errors === 0) {
        document.getElementById("cButton").style.display = "none";
        document.getElementById("cLoader").style.display = "flex";
        axios.post(this.axiosPostUrl, this.form)
        .then(function() {
          document.getElementById("cLoader").style.display = "none";
          document.getElementById("cSuccess").style.display = "block";
        })
        .catch(function() {
          document.getElementById("cLoader").style.display = "none";
          document.getElementById("cError").style.display = "block";
        });
      }
    }
  },
}

</script>

<style scoped>

.contactBG {
  width: 100%;
  color: var(--font-color-1-contact);
  background-color: var(--bg-color-1-contact);
}

.contactWrapper {
  width: 70%;
  margin: 300px auto 0 auto;
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
  background-color: var(--bg-color-2-contact);
}

form {
  width: 75%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cRow {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 10px 0 0 0;
}

.cRequiredMessages {
  display: none;
  width: 100%;
  margin: 5px 0;
  font-weight: bold;
  text-align: left;
  letter-spacing: 1px;
  color: var(--font-color-error-contact);
}

.cTitle {
  width: 80%;
  text-align: center;
  font-size: 50px;
  font-family: var(--main-title-font-family);
}

.cSubtitle {
  width: 70%;
  padding: 15px 0;
  text-align: center;
  line-height: 1.4;
}

.cLabelGeneral {
  width: 100%;
  padding: 20px 0 10px 0;
  text-align: left;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
  font-size: 15px;
}

input[type=text], select, textarea {
  display: flex;
  padding: 5px;
  letter-spacing: 1px;
  font-size: 20px;
  transition: 0.25s;
  border: 1px solid var(--border-color-contact);
  font-family: var(--main-website-font-family);
}

textarea {
  height: 200px;
  resize: none;
}

input[type=text]:hover, select:hover, textarea:hover {
  box-shadow: 1px 1px 5px var(--box-shadow-color-1-contact), -1px -1px 5px var(--box-shadow-color-2-contact);
  transition: 0.25s;
}

input[type=text]:focus, select:focus, textarea:focus {
  outline: none;
  box-shadow: 1px 1px 5px var(--box-shadow-color-1-contact), -1px -1px 5px var(--box-shadow-color-2-contact);
  transition: 0.25s;
}

/* RADIO Checkboxes */
.cCheckboxes {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.cCheckbox {
  width: 100%;
  margin: 10px auto 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cCheckbox label {
  width: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  padding-left: 5px;
  font-size: 20px;
}

/* PRIVACY Checkbox */
.cTerms {
  width: 100%;
  display: flex;
  line-height: 1.3;
  font-size: 18px;
}
.cTerms label {
  padding-left: 5px;
  text-align: justify;
}

/* BUTTON */
#cButton {
  width: max-content;
  max-width: 90%;
  margin: 30px auto 0 auto;
  padding: 10px 45px;
  letter-spacing: 1px;
  text-align: center;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
  opacity: 1;
  transition: opacity ease 0.5s;
  color: var(--button-font-color-contact);
  background-color: var(--button-bg-color-contact);
  font-family: var(--main-website-font-family);
}
#cButton:hover {
  opacity: 0.8;
  transition: opacity ease 0.5s;
}

/* LOADER */
#cLoader {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#cLoader img {
  width: 35px;
  height: 35px;
  margin: 0 auto;
  padding: 30px 0 10px 0;
}

/* SUCCESS, ERROR BOXES */
.sentBox {
  display: none;
  width: 95%;
  margin-top: 30px;
  padding: 10px;
  text-align: center;
  font-size: 20px;
}
.sentBox a {
  text-decoration: underline;
}
#cSuccess {
  background-color: var(--success-box-color-1-contact);
  border: 1px solid var(--success-box-color-2-contact);
}
#cError {
  background-color: var(--fail-box-color-1-contact);
  border: 1px solid var(--fail-box-color-2-contact);
}

@media screen and (max-width: 1100px) {

  .contactWrapper {
    width: 90%;
  }

  form {
    width: 85%;
  }

}

@media screen and (max-width: 800px) {

  .contactWrapper {
    width: 90%;
  }

  form {
    width: 90%;
  }

  .cTitle {
    font-size: 50px;
  }

  #cButton {
    font-size: 17px;
  }

}

@media screen and (max-width: 500px) {

  .contactWrapper {
    margin: 0 auto;
    padding: 0 0 30px 0;
  }

  .contactBG {
    padding: 0;
  }

  .contactWrapper {
    width: 100%;
  }

  form {
    width: 90%;
  }

  .cTitle {
    width: 80%;
    padding-top: 15px;
    font-size: 12vw;
  }

  .cSubtitle {
    width: 85%;
    font-size: 20px;
  }

  #cButton {
    font-size: 4vw;
  }

}

@media screen and (max-width: 320px) {

  #cButton {
    font-size: 5vw;
  }
}

</style>