<template>
  <div class="fadingSlider">
    <div v-for="(testi, index) in testimonials" class="general" :key="index">
      <div class="wrapper">
        <div class="left">
          <div class="numWrapper">
            <div>0{{ index + 1 }}</div>
            <div>0{{ testimonials.length }}</div>
          </div>
          <div class="imgWrapper">
            <hr><img :src="testi.imgSrc" :alt="testi.imgAlt" width="720" height="480">
          </div>
          <div class="btnWrapper">
            <button class="nextPrevBtn" v-on:click="prev">Zurck</button>
            <button class="nextPrevBtn" v-on:click="next">Weiter</button>
          </div>
        </div>
        <div class="right">
          <div class="text testiTitle">{{ testi.title }}</div>
          <div class="text main">{{ testi.text }}</div>
          <div class="text linkWrapper">
            <MainButtonDark :link="testi.link" :button-text="btnText"></MainButtonDark>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import MainButtonDark from "@/components/buttons/MainButtonDark";

export default {
  name: "TestiMonials",
  components: { MainButtonDark },
  props: [ "testimonials", "btnText" ],
  mounted() {
    /* eslint-env jquery */
    $('.fadingSlider').slick({
      dots: false,
      speed: 500,
      fade: true,
      infinite: true,
      arrows: false,
      cssEase: 'linear',
      touchThreshold: 50,
      adaptiveHeight: true,
    });
  },
  methods: {
    next: function() {
      $('.fadingSlider').slick('slickNext');
    },
    prev: function() {
      $('.fadingSlider').slick('slickPrev');
    }
  }
}

</script>

<style scoped>

.fadingSlider .general {
  padding: 0;
  margin: 30px 0 60px 0;
  height: inherit;
}

.fadingSlider .wrapper {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.fadingSlider .left {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.fadingSlider .numWrapper {
  width: 30vw;
  margin: auto;
  padding: 0 0 5vw 0;
  display: flex;
  font-size: 17px;
  justify-content: space-between;
}

.fadingSlider .imgWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  width: 30vw;
}

.fadingSlider .imgWrapper hr {
  position: absolute;
  margin: auto;
  width: 35vw;
  border: none;
  height: 1px;
  transform: rotate(45deg);
  background-color: var(--hr-color-testimonial-section-home);
}

.fadingSlider img {
  margin: auto;
  width: 30vw;
  max-height: 20vw;
  object-fit: cover;
  z-index: 1;
}

.fadingSlider .btnWrapper {
  width: 30vw;
  margin: auto;
  padding: 5vw 0 0 0;
  display: flex;
  justify-content: space-between;
}

.fadingSlider .nextPrevBtn {
  border: none;
  cursor: pointer;
  font-size: 17px;
  letter-spacing: 1px;
  text-decoration: underline;
  text-transform: uppercase;
  background-color: transparent;
  font-family: var(--main-website-font-family);
}

.fadingSlider .right {
  display: flex;
  width: 100%;
  margin: auto 0;
  flex-direction: column;
  text-align: left;
  line-height: 1.5;
  font-size: 20px;
}

.fadingSlider .text {
  width: 80%;
}

.fadingSlider .testiTitle {
  width: 90%;
  font-size: 35px;
  padding: 30px 0;
  margin-bottom: 0;
}

.fadingSlider .main {
  border-left: 1px solid;
  padding: 0 0 0 50px;
}

.fadingSlider .linkWrapper {
  width: 90%;
  padding: 30px 0;
}

@media screen and (max-width: 1080px) {

  .fadingSlider .wrapper {
    flex-direction: column;
  }

  .fadingSlider .text {
    width: 80%;
    margin: auto;
  }

  .fadingSlider .numWrapper {
    width: 80%;
    padding: 3vw 0 1vw 0;
  }

  .fadingSlider .imgWrapper {
    width: 90vw;
  }

  .fadingSlider .imgWrapper hr {
    width: 400px;
  }

  .fadingSlider img {
    width: 300px;
    height: 300px;
    max-height: 300px;
    object-fit: cover;
    padding: 10px;
    border-radius: 50%;
    border: 2px solid var(--border-color-testimonial-section-home);
  }

  .fadingSlider .btnWrapper {
    width: 80%;
    padding: 1vw 0 3vw 0;
  }

  .fadingSlider .testiTitle {
    text-align: center;
    padding-top: 15px;
    padding-bottom: 10px;
  }

  .fadingSlider .main {
    border-left: none;
    padding: 0;
    text-align: center;
  }

}

@media screen and (max-width: 400px) {

  .fadingSlider img {
    width: 200px;
    height: 200px;
  }

  .fadingSlider .imgWrapper hr {
    width: 250px;
  }

  .fadingSlider .testiTitle {
    font-size: 9vw;
  }

}

</style>