<template>
  <div class="video">
    <div id="videoTitle">
      <SectionTitle :title="heading"></SectionTitle>
    </div>
    <div class="videos">
      <div id="leftVideo" class="vidBox">
        <PlayIcon v-on:click.native="playVideo($event, 'thumbnailLeft', 'leftIframe')"></PlayIcon>
        <img id="thumbnailLeft" :src="videoLeft.thumbnail" :alt="videoLeft.alt">
        <iframe id="leftIframe" :title="videoLeft.title" :src="videoLeft.src" :allow="videoLeft.allow" loading="lazy"></iframe>
        <div class="videoDescriptionWrapper">
          <div class="videoTitle">{{ videoLeft.title }}</div>
          <hr>
          <div class="videoSubtitle">{{ videoLeft.subtitle }}</div>
        </div>
      </div>
      <div id="rightVideo" class="vidBox">
        <PlayIcon v-on:click.native="playVideo($event, 'thumbnailRight', 'rightIframe')"></PlayIcon>
        <img id="thumbnailRight" :src="videoRight.thumbnail" :alt="videoRight.alt">
        <iframe id="rightIframe" :title="videoRight.title" :src="videoRight.src" :allow="videoRight.allow" loading="lazy"></iframe>
        <div class="videoDescriptionWrapper">
          <div class="videoTitle">{{ videoRight.title }}</div>
          <hr>
          <div class="videoSubtitle">{{ videoRight.subtitle }}</div>
        </div>
      </div>
    </div>
    <div id="videoButton">
      <MainButton :link="buttonLink" :button-text="buttonText"></MainButton>
    </div>
  </div>
</template>

<script>

import SectionTitle from "@/components/titles/SectionTitle";
import MainButton from "@/components/buttons/MainButton";
import PlayIcon from "@/components/icons/PlayIcon";

export default {
  name: "SectionVideo",
  components: { PlayIcon, MainButton, SectionTitle },
  data() {
    return {
      heading: "Video",
      buttonText: "Mehr Filme",
      buttonLink: "/hochzeitsvideografie",
      videoLeft: {
        allow: "autoplay; fullscreen; picture-in-picture;",
        title: "Wedding Trailer Left",
        subtitle: "Traumhaftes Hochzeitsshooting in Dubai, Love",
        src: "https://player.vimeo.com/video/426412008?h=5fb3d9c08f&sidedock=0&title=0&loop=1&muted=0&byline=0&portrait=0&autopause=0&app_id=122963",
        thumbnail: "/pub/img/video/thumbnail-1.jpg",
        alt: "Left Image Description"
      },
      videoRight: {
        allow: "autoplay; fullscreen; picture-in-picture;",
        title: "Wedding Trailer Right",
        subtitle: "Some Description",
        src: "https://player.vimeo.com/video/401522694?h=cd208f189d&sidedock=0&title=0&loop=1&muted=0&byline=0&portrait=0&autopause=0&app_id=122963",
        thumbnail: "/pub/img/video/thumbnail-3.jpg",
        alt: "Right Image Description"
      }
    }
  },
  methods: {
    playVideo: function(event, imgID, iframeID) {
      event.target.style.display = "none";
      document.getElementById(imgID).style.display = "none";
      let iframe = document.getElementById(iframeID);
      iframe.style.display = "block";
      // eslint-disable-next-line no-undef
      let player = new Vimeo.Player(iframe);
      player.play();
    }
  }
}

</script>

<style scoped>

.video {
  margin: 0;
  background-color: var(--bg-color-video-section-home);
}

#videoTitle {
  margin-bottom: 20px;
}

.videos {
  margin: 50px auto 0 auto;
  display: flex;
}

.vidBox {
  position: relative;
  width: 40vw;
  max-width: 640px;
  margin: 0 auto;

}

.vidBox:before {
  content: "";
  display: block;
  padding-top: 56%; /* 16:9 format */
}

.vidBox iframe {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 0;
}

.vidBox img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  max-width: 640px;
  max-height: 360px;
  z-index: 15;
  box-shadow:  -2vw 0 0 var(--bg-color-video-section-home),
                2vw 0 0 var(--bg-color-video-section-home),
                0 1vw 0 var(--bg-color-video-section-home),
                0 -2vw 0 var(--bg-color-video-section-home);
}

iframe {
  width: 100%;
  margin: 0 auto;
}

svg {
  position: absolute;
  margin: 0 auto;
  width: 100%;
  height: 20%;
  top: 40%;
  cursor: pointer;
  z-index: 20;
  -webkit-tap-highlight-color: transparent;
  fill: var(--bg-color-playbutton-video-section-home);
}

.videoDescriptionWrapper {
  width: 100%;
  margin: 0 auto;
  position: absolute;
  padding-top: 30px;
  text-align: center;
}

.videoTitle {
  font-size: 30px;
}

.videoSubtitle {
  font-size: 25px;
}

hr {
  width: 50%;
}

#videoButton {
  padding: 130px 0 50px 0;
}

@media screen and (max-width: 1400px) {

  .videoTitle {
    font-size: 25px;
  }

  .videoSubtitle {
    font-size: 20px;
  }

}

@media screen and (max-width: 1080px) {

  .vidBox {
    max-width: 1080px;
    width: 90vw;
  }

  .vidBox img {
    max-width: 1080px;
    max-height: 100%;
    width: 90vw;
  }

  #rightVideo {
    display: none;
  }

  #videoButton {
    padding-top: 140px;
  }

}

@media screen and (max-width: 500px) {

  .videoTitle {
    font-size: 6vw;
  }

  .videoSubtitle {
    font-size: 5vw;
  }

  #videoButton {
    padding-top: 160px;
  }

}

@media screen and (max-width: 320px) {

  #videoButton {
    padding-top: 135px;
  }

}

</style>