<template>
  <div class="slider">
    <img v-for="(img, index) in images" v-bind:key="index" class="sliderImages"
         :src="img.url" :alt="img.alt" width="720" height="480">
  </div>
</template>

<script>

export default {
  name: "SliderOne",
  props: [ "images" ],
  mounted() {
    /* eslint-env jquery */
    $(document).ready(() => {
      $('.slider').slick({
        dots: false,
        arrows: false,
        autoplay: true,
        touchThreshold: 50,
        pauseOnFocus: "false",
        pauseOnHover: "false"
      });
    });
  },
}

</script>

<style>

.slick-list {
  height: max-content;
}

</style>

<style scoped>

.slider {
  overflow: hidden;
  background-color: var(--bg-color-slider-home);
}

.sliderImages {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

</style>