<template>
  <div id="vimeoWholeWrapper">
    <div id="leftVideo" class="vidBox">
      <div id="vimeoSvgWrapper">
        <PlayIcon v-on:click.native="playVideo($event, video.imgID, video.iframeID)"></PlayIcon>
      </div>
      <img :id="video.imgID" :src="video.thumbnail" :alt="video.alt">
      <iframe :id="video.iframeID" :title="video.title" :src="video.src" :allow="video.allow" loading="lazy"></iframe>
    </div>
    <div class="videoDescriptionWrapper">
      <div class="videoTitle">{{ video.title }}</div>
      <div class="videoSubtitle">{{ video.subtitle }}</div>
    </div>
  </div>
</template>

<script>

import PlayIcon from "@/components/icons/PlayIcon";

export default {
  name: "VidBoxVimeo",
  components: { PlayIcon },
  props: [ "video" ],
  methods: {
    playVideo: function(event, imgID, iframeID) {
      event.target.style.display = "none";
      document.getElementById(imgID).style.display = "none";
      let iframe = document.getElementById(iframeID);
      iframe.style.display = "block";
      // eslint-disable-next-line no-undef
      let player = new Vimeo.Player(iframe);
      player.play();
    }
  }
}

</script>

<style scoped>

#vimeoWholeWrapper {
  background-color: var(--bg-color-video);
}

.vidBox {
  position: relative;
  width: 90%;
  max-width: 1080px;
  margin: 0 auto;
}

.vidBox:before {
  content: "";
  display: block;
  padding-top: 56%; /* 16:9 format */
}

.vidBox iframe {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;;
  left: 0;
  right: 0;
  bottom: 0;
  border: 0;
}

.vidBox img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  max-width: 1080px;
  max-height: 720px;
  z-index: 15;
  box-shadow:  -5px 0 0 var(--bg-color-video),
                5px 0 0 var(--bg-color-video),
                0 5px 0 var(--bg-color-video),
                0 -5px 0 var(--bg-color-video);
}

iframe {
  width: 100%;
  margin: 0 auto;
}

#vimeoSvgWrapper {
  width: 100%;
  display: flex;
  margin: 0 auto;
  justify-content: center;
}

svg {
  position: absolute;
  margin: 0 auto;
  width: 100%;
  height: 20%;
  top: 40%;
  cursor: pointer;
  z-index: 20;
  -webkit-tap-highlight-color: transparent;
  fill: var(--bg-color-playbutton-video);
}

.videoDescriptionWrapper {
  position: relative;
  margin: 0 auto;
  width: 90%;
  max-width: 1080px;
  padding-top: 20px;
  text-align: center;
  letter-spacing: 1px;
  line-height: 1.2;
}

.videoTitle {
  margin-bottom: 10px;
  font-size: 60px;
  font-family: var(--main-title-font-family);
}

.videoSubtitle {
  font-size: 25px;
  text-transform: uppercase;
}

@media screen and (max-width: 678px) {

  .videoTitle {
    font-size: 40px;
  }

  .videoSubtitle {
    font-size: 20px;
  }

}

@media screen and (max-width: 500px) {

  .videoDescriptionWrapper {
    padding-top: 15px;
  }

}

</style>