<template>
  <div id="menuWrapper">

    <!-- DESKTOP -->
    <div v-if="desktopView" class="menu">
      <!-- Logo -->
      <router-link to="/">
        <div class="logo">
          <img :src="logoSrc" :alt="logoAlt" width="70" height="70">
        </div>
      </router-link>
      <div class="mainPointsAndIconsWrapper">
        <!-- Main Points -->
        <template v-for="(point, index) in menu">
          <router-link v-if="point.main" :to="point.link" :key="'single' + index">
            <div class="menuPoints">{{ point.title }}</div>
          </router-link>
          <div v-else class="menuPoints" :key="'single' + index">{{ point.title }}
            <div class="subPoints">
              <div class="gap"></div>
              <div class="subPointsWrapper" >
                <template v-for="(subpoint, index) in point.link">
                  <router-link :to="subpoint.link" :key="'sub' + index">
                    <div class="subPoint">{{ subpoint.title }}</div>
                  </router-link>
                </template>
              </div>
            </div>
          </div>
        </template>
        <!-- Icons -->
        <div class="icons">
          <a :href="socials.fb" target="_blank"><FaceBook1></FaceBook1></a>
          <a :href="socials.it" target="_blank"><InstaGram1></InstaGram1></a>
          <a :href="socials.yt" target="_blank"><YouTube1></YouTube1></a>
        </div>
      </div>
    </div>

    <!-- MOBILE -->
    <div v-else class="menuMobile">
      <!-- Logo -->
      <router-link to="/">
        <div class="logoMobile">
          <img :src="logoSrc" :alt="logoAlt" width="70" height="70">
        </div>
      </router-link>
      <!-- Hamburger -->
      <div id="hamburger" class="hamburger hamburger--squeeze" v-on:click="toggleMenu">
        <span class="hamburger-box"><span class="hamburger-inner"></span></span>
      </div>
      <div id="mainMenuMobile">
        <!-- Main Points -->
        <div class="main">
          <template v-for="(point, index) in menu">
            <div v-if="point.main" v-bind:key="'single' + index" class="animate">
              <router-link v-bind:to="point.link" class="menuPointsMobile">{{ point.title }}</router-link>
            </div>
            <template v-else v-for="(subpoint, index) in point.link">
              <div v-bind:key="'sub' + index" class="animate">
                <router-link v-bind:to="subpoint.link" class="menuPointsMobile">{{ subpoint.title }}</router-link>
              </div>
            </template>
          </template>
          <!-- Icons -->
          <div class="iconsMobile move">
            <a :href="socials.fb" target="_blank"><FaceBook1></FaceBook1></a>
            <a :href="socials.it" target="_blank"><InstaGram1></InstaGram1></a>
            <a :href="socials.yt" target="_blank"><YouTube1></YouTube1></a>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>

import menu from "@/menu";
import socials from "@/socials";
import FaceBook1 from "@/components/icons/FaceBook1";
import InstaGram1 from "@/components/icons/InstaGram1";
import YouTube1 from "@/components/icons/YouTube1";

export default {
  name: "MenuBasic1",
  components: { YouTube1, InstaGram1, FaceBook1 },
  data() {
    return {
      desktopView: false,
      menu: menu,
      logoSrc: "/pub/img/logo/logo.png",
      logoAlt: "Logo Description",
      socials: {
        fb: socials.fb,
        it: socials.it,
        yt: socials.yt
      }
    }
  },
  beforeMount() {
    // DESKTOP OR MOBILE VIEW?
    let vw = window.innerWidth;
    if (vw > 1023) { this.desktopView = true; }
    else           { this.desktopView = false; }

    // CALCULATE & SET INITIAL VH VALUE
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  },
  mounted() {
    // DESKTOP OR MOBILE VIEW?
    window.addEventListener('resize', () => {
      let vw = window.innerWidth;
      if (vw > 1023) { this.desktopView = true; }
      else           { this.desktopView = false; }
    }, { passive: true });

    // SCROLL BEHAVIOUR
    document.documentElement.style.overflow = "visible"; // html scroll
    document.body.style.overflow ="visible";             // body scroll

    // ADD LISTENER FOR VH VALUE IN CASE OF WINDOW RESIZING
    window.addEventListener('resize', () => {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
      if (window.innerWidth > 1023) {
        document.documentElement.style.overflow = "visible"; // html scroll
        document.body.style.overflow = "visible";            // body scroll
      }
    });
  },
  methods: {
    toggleMenu() {
      let toggle = document.getElementById("hamburger")
      let menu = document.getElementById("mainMenuMobile")
      let animatedMenuPoints = document.getElementsByClassName("animate")
      let html = document.documentElement
      let body = document.body
      if (toggle.classList.contains("is-active")) {
        toggle.classList.remove("is-active")
        menu.style.display = "none"
        html.style.overflow = "visible"
        body.style.overflow ="visible"
        for (let i = 0; i < animatedMenuPoints.length; i++) {
          animatedMenuPoints[i].classList.remove("move")
        }
      } else {
        toggle.classList.add("is-active")
        menu.style.display = "block"
        html.style.overflow = "hidden"
        body.style.overflow = "hidden"
        for (let i = 0; i < animatedMenuPoints.length; i++) {
          animatedMenuPoints[i].classList.add("move")
        }
      }
    }
  }
}

</script>

<style scoped>

/* DESKTOP MENU WHOLE */
.menu {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px 0;
  box-shadow: 0 0 15px var(--box-shadow-color-menu-1);
  background-color: var(--bg-color-menu-1);
}

/* DESKTOP LOGO */
.logo {
  display: flex;
  vertical-align: center;
}
.logo img {
  height: 70px;
  width: 70px;
}

/* DESKTOP MAIN POINTS */
.mainPointsAndIconsWrapper {
  display: flex;
  align-items: center;
  color: var(--font-color-menu-1);
}
.mainPointsAndIconsWrapper a {
  text-decoration: none;
}
.menuPoints {
  font-size: var(--font-size-menu-1);
  letter-spacing: 4px;
  padding-left: 4px;
  border-bottom: 2px solid transparent;
  transition: border-bottom-color 200ms;
  margin-right: 30px;
}
.menuPoints:hover {
  cursor: pointer;
  border-bottom: 2px solid var(--font-color-menu-1);
  transition: border-bottom-color 200ms;
}
.subPoints {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  align-content: center;
  transition: opacity 0.3s linear 0s;
}
.subPointsWrapper {
  text-align: left;
  padding: 10px;
  background-color: var(--bg-color-menu-subpoints-1);
}
.menuPoints:hover .subPoints {
  visibility: visible;
  opacity: 1;
  cursor: default;
}
.subPoint {
  margin: 10px 0;
  cursor: pointer;
  color: var(--font-color-menu-subpoints-1);
}
.gap {
  height: 20px;
  cursor: auto;
}

/* DESKTOP ICONS */
.icons {
  display: flex;
  align-items: center;
  justify-content: center;
}
.icons a {
  padding: 0 10px;
}
.icons svg {
  display: flex;
  opacity: 0.7;
  width: var(--font-size-menu-1);
  height: var(--font-size-menu-1);
  fill: var(--font-color-menu-1);
  transition: opacity linear 100ms;
}
.icons svg:hover {
  opacity: 1;
  transition: opacity linear 100ms;
}

/* MOBILE MENU WHOLE */
.menuMobile {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
  box-shadow: 0 0 15px var(--box-shadow-color-menu-1);
  background-color: var(--bg-color-menu-1);
}

/* MOBILE LOGO */
.logoMobile {
  display: flex;
  vertical-align: center;
}
.logoMobile img {
  height: 70px;
  width: 70px;
  margin: 0 0 0 5vw;
}

/* MOBILE MAIN */
#mainMenuMobile {
  width: 100%;
  display: none;
  position: absolute;
  overflow: hidden;
  top: 0;
  margin: 0;
  z-index: 1000;
  letter-spacing: 3px;
  background-color: var(--bg-color-menu-1);
}
.main {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.main div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px 0;
  padding: 0;
}
.main div:nth-child(1) {
  animation-delay: 0.1s;
}
.main div:nth-child(2) {
  animation-delay: 0.2s;
}
.main div:nth-child(3) {
  animation-delay: 0.3s;
}
.main div:nth-child(4) {
  animation-delay: 0.4s;
}
.main div:nth-child(5) {
  animation-delay: 0.5s;
}
.main div:nth-child(6) {
  animation-delay: 0.6s;
}
.main div:nth-child(7) {
  animation-delay: 0.7s;
}
.main div:nth-child(8) {
  animation-delay: 0.8s;
}
.move {
  animation-duration: 400ms;
  animation-fill-mode: both;
  animation-name: fadeInUp;
}
.menuPointsMobile {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: var(--font-size-mobile-menu-1);
  color: var(--font-color-menu-1);
}

/* MOBILE ICONS */
.iconsMobile a {
  padding: 5px 10px 0 10px;
}
.iconsMobile svg {
  opacity: 0.9;
  width: var(--font-size-mobile-menu-1);
  height: var(--font-size-mobile-menu-1);
  fill: var(--font-color-menu-1);
}

/* MOBILE HAMBURGER */
.hamburger {
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  border: 0;
  height: 20px;
  margin: 0 5vw 0 0;
  z-index: 10000;
  background-color: transparent;
}
.hamburger:hover {
  opacity: 0.7;
}
.hamburger.is-active:hover {
  opacity: 0.7;
}
.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: var(--font-color-menu-1);
}
.hamburger-box {
  display: inline-block;
  position: relative;
  padding: 0;
  width: 25px;
  height: 5px;
}
.hamburger-inner {
  display: block;
}
.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
  width: 25px;
  height: 1px;
  background-color: var(--font-color-menu-1);
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.hamburger-inner::before, .hamburger-inner::after {
  content: "";
  display: block;
}
.hamburger-inner::before {
  top: -8px;
}
.hamburger-inner::after {
  bottom: -8px;
}
.hamburger--squeeze .hamburger-inner {
  transition-duration: 0.075s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.hamburger--squeeze .hamburger-inner::before {
  transition: top 0.075s 0.12s ease, opacity 0.075s ease;
}
.hamburger--squeeze .hamburger-inner::after {
  transition: bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.hamburger--squeeze.is-active .hamburger-inner {
  transform: rotate(45deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
.hamburger--squeeze.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
  transition: top 0.075s ease, opacity 0.075s 0.12s ease;
}
.hamburger--squeeze.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(-90deg);
  transition: bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}

</style>