<template>
  <div class="photo">
    <div id="photoTitle">
      <SectionTitle :title="heading"></SectionTitle>
    </div>
    <div class="sliderCenter">
      <ImgBox v-for="(img, index) in images"
              :image-source="img.src"
              :image-title="img.title"
              :image-destination="img.destination"
              :key="index"></ImgBox>
    </div>
    <div id="photoButton">
      <MainButton :link="buttonLink" :button-text="buttonText"></MainButton>
    </div>
  </div>
</template>

<script>

import MainButton from "@/components/buttons/MainButton";
import SectionTitle from "@/components/titles/SectionTitle";
import ImgBox from "@/components/imgTxt/ImgBox";

export default {
  name: "SectionPhoto",
  components: { ImgBox, SectionTitle, MainButton },
  data() {
    return {
      heading: "Foto",
      buttonText: "Weitere Gallerien",
      buttonLink: "/hochzeitsfotografie",
      images: [
        {
          src: "/pub/img/photo/gallery-1/img-2.jpg",
          title: "Berlin",
          destination: "/blog"
        },
        {
          src: "/pub/img/photo/gallery-1/img-3.jpg",
          title: "Kln",
          destination: "/blog"
        },
        {
          src: "/pub/img/photo/gallery-1/img-5.jpg",
          title: "Mnchen",
          destination: "/blog"
        },
        {
          src: "/pub/img/photo/gallery-1/img-7.jpg",
          title: "Hamburg",
          destination: "/blog"
        },
        {
          src: "/pub/img/photo/gallery-1/img-9.jpg",
          title: "Bremen",
          destination: "/blog"
        },
        {
          src: "/pub/img/photo/gallery-1/img-1.jpg",
          title: "Dsseldorf",
          destination: "/blog"
        }
      ]
    }
  },
  mounted() {
    /* eslint-env jquery */
    $(document).ready(() => {
      $('.sliderCenter').slick({
        dots: true,
        arrows: false,
        autoplay: true,
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        touchThreshold: 50,
        responsive: [
          {
            breakpoint: 1401,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 1,
              infinite: true,
              dots: true,
              arrows: false
            }
          },
          {
            breakpoint: 901,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              dots: true,
              arrows: false
            }
          },
        ]
      });
    });
  }
}

</script>

<style>

.slick-slide {
  margin: 0;
}

.slick-list {
  height: max-content;
}

.slick-dots li button:before {
  content: "";
  opacity: 0.4;
  font-size: 8px;
}

.slick-dots li.slick-active button {
  opacity: 1;
}

</style>

<style scoped>

.photo {
  margin: 0;
  background-color: var(--bg-color-photo-section-home);
}

.sliderCenter {
  display: flex;
  width: 90vw;
  margin: 0 auto;
  align-items: center;
}

#photoTitle {
  margin-bottom: 20px;
}

#photoButton {
  padding: 50px 0;
}

</style>